@import url(./settings.scss);

/*set wr-header*/

header {
    .container {
        max-width: 1720px;
        height: 160px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        .header-logo {
            width: 172px;
            height: 69px;
        }
        .burger {
            width: 125px;
            height: 55px;
            position: absolute;
            right: 25px;
            cursor: pointer;
            .burger-text {
                font-family: 'MBold';
                text-transform: uppercase;
                font-size: 13px;
                letter-spacing: 2.6px;
                color: #c4c4c4;
            }
            .burger-img {
                display: inline-block;
                transition: all .4s;
                box-sizing: border-box;
                position: relative;
                width: 40px;
                height: 23px;
                span {
                    display: inline-block;
                    transition: all .4s;
                    box-sizing: border-box;
                    position: absolute;
                    width: 100%;
                    height: 3px;
                    background-color: #000;
                }
                span:nth-of-type(1) {
                    top: 11px;
                }
                span:nth-of-type(2) {
                    top: 17px;
                }
                span:nth-of-type(3) {
                    top: 25px;
                }
            }
            .first {
                transform: translate(4px, -8px) rotate(45deg);
                transform-origin: left top;
            }
            .middle {
                transform: scale(0);
            }
            .last {
                transform: translate(-8px, -24px) rotate(-45deg);
                transform-origin: right bottom;
            }
        }
    }
}

nav {
    .container {
        max-width: 1720px;
        width: 100%;
        height: 750px;
        background-color: #e9edef;
        position: absolute;
        visibility: hidden;
        z-index: 10;
        .menu-list {
            display: flex;
            list-style: none;
            flex-direction: column;
            align-items: flex-start;
            height: 100%;
            justify-content: center;
            margin-left: 20%;
            position: relative;
            >:nth-child(2):after {
                position: absolute;
                content: '';
                border-bottom: 1px solid #000000;
                width: 35px;
                height: 1px;
                top: 29px;
                left: 215px;
            }
            >:nth-child(3):after {
                position: absolute;
                content: '';
                border-bottom: 1px solid #000000;
                width: 35px;
                height: 1px;
                top: 29px;
                left: 215px;
            }
            li {
                padding: 15px 0px 15px 0;
                position: relative;
                display: flex;
                align-items: center;
                a {
                    text-decoration: none;
                    color: #000000;
                    font-family: 'MBold';
                    font-size: 30px;
                    text-transform: uppercase;
                    letter-spacing: 3px;
                }
                a:hover {
                    color: #e06f0a;
                }
                .menu-list-about {
                    list-style: none;
                    display: flex;
                    justify-content: space-between;
                    li {
                        display: inline-block;
                        padding: 0 20px;
                        a {
                            height: 12px;
                            color: #626262;
                            font-family: 'MReg';
                            font-size: 14px;
                            text-transform: uppercase;
                            letter-spacing: 2.8px;
                        }
                        a:hover {
                            color: #e06f0a;
                            letter-spacing: 2.8px;
                        }
                    }
                    >::after {
                        content: '\002f';
                        position: absolute;
                        right: -5px;
                        color: #626262;
                        font-family: 'MReg';
                        font-size: 14px;
                        font-weight: 700;
                        text-transform: uppercase;
                        letter-spacing: 2.8px;
                    }
                    >:nth-child(3):after {
                        content: '';
                    }
                }
                .menu-list-services {
                    list-style: none;
                    display: flex;
                    justify-content: space-between;
                    margin-left: 115px;
                    li {
                        padding: 0 20px;
                        a {
                            display: block;
                            height: 12px;
                            color: #626262;
                            font-family: 'MReg';
                            font-size: 14px;
                            font-weight: semibold;
                            text-transform: uppercase;
                            letter-spacing: 2.8px;
                        }
                        a:hover {
                            color: #e06f0a;
                            letter-spacing: 2.8px;
                        }
                    }
                    >::after {
                        content: '\002f';
                        position: absolute;
                        right: -5px;
                        color: #626262;
                        font-family: 'MReg';
                        font-size: 14px;
                        font-weight: 700;
                        text-transform: uppercase;
                        letter-spacing: 2.8px;
                    }
                    >:nth-child(4):after {
                        content: '';
                    }
                }
            }
        }
    }
}


/*set wr-slides*/

.wr-slides {
    .container {
        max-width: 1720px;
        height: 1040px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .slider {
            width: 1600px;
            height: 850px;
            .slick-next,
            .slick-prev {
                height: 59px;
                width: 59px;
                background: #7f7f7f;
                z-index: 2;
            }
        }
        .scroll {
            width: 134px;
            height: 190px;
            padding: 52px 794px 122px 797px;
            position: relative;
            a {
                text-decoration: none;
                text-transform: capitalize;
                &::after {
                    content: '\2193';
                    position: absolute;
                    width: 12px;
                    height: 24px;
                    top: 50px;
                    right: 805px;
                    color: #a5a5a5;
                }
            }
        }
    }
    .slider-text {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 200px;
        right: 940px;
        p::before {
            content: '';
            position: absolute;
            width: 40px;
            height: 4px;
            top: -10px;
            right: 390px;
            background-color: #000;
        }
        & p:nth-child(1),
        & p:nth-child(3) {
            font-family: 'Segoe';
            font-size: 60px;
            font-weight: 300;
            text-align: left;
            color: #626262;
            margin-bottom: 5px;
            margin-top: 0;
        }
        & p:nth-child(2) {
            font-family: 'SegoeB';
            font-size: 100px;
            text-align: left;
            text-transform: uppercase;
            color: #000001;
            margin-bottom: 0;
            margin-top: 0;
        }
        .btn {
            width: 220px;
            height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 2px;
            border: solid 2px #000000;
            cursor: pointer;
            margin-top: 55px;
            a {
                font-family: 'MBold';
                font-size: 13px;
                font-weight: bold;
                font-stretch: normal;
                font-style: normal;
                line-height: normal;
                letter-spacing: 2.6px;
                text-align: center;
                color: #e06f0a;
                text-decoration: none;
                text-transform: uppercase;
            }
        }
    }
}